import { OrderByModel } from "../components/core/TableModel";
import { MyAxios } from "../helper/myaxios";
import { PaginationRequest } from "../models/baseModel";

export class MailTemplate {
    static GetList(paginationRequest: PaginationRequest, then?: any, exp?: any, final?: any) {
        var url = `mails/templates?${paginationRequest.generateQueryString()}`;
        return MyAxios.Get(url, then, exp, final);
    }

    static GetDetail(key: string | undefined, then?: any, exp?: any, final?: any) {
        var url = `mails/templates/${key}`;
        return MyAxios.Get(url, then, exp, final);
    }

    static Detail(code: string | undefined, then?: any, exp?: any, final?: any) {
        var url = `mails/templates/${code}`;
        return MyAxios.Get(url, then, exp, final);
    }

    static Add(data: any, then?: any, exp?: any, final?: any) {
        var url = `mails/templates`;
        return MyAxios.Post(url, data, then, exp, final);
    }

    static Edit(key: string | undefined, data: any, then?: any, exp?: any, final?: any) {
        var url = `mails/templates/${key}`;
        return MyAxios.Put(url, data, then, exp, final);
    }

    static Delete(key: string | undefined, timestamp: string, then?: any, exp?: any, final?: any) {
        var url = `mails/templates/${key}?timestamp=${encodeURIComponent(timestamp)}`;
        return MyAxios.Delete(url, then, exp, final);
    }
}

